.long-step{
    width: 100%;
    height: 125px;
    margin: 15px;
    border: solid;
    border-color: white;
    border-width: 10px;
    border-radius: 35px;
    background-color: rgb(253, 243, 243);
    background-color: white;
    padding: 0 9px ;
    font-family: 'Abel', sans-serif;

    .title{
        height: 30px;
        width: 100%;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-direction: column;
        border: solid;
        border-width: 0 0 1px 0;
        font-weight: 800;
    }
    .description {
        height: 45px;
        width: 100%;

    }
    .actions {
        width: 100%;
        .meeting {
            height: 30px;
            width: 100%;
            border: solid;
            border-width: 1px 0 1px 0;
            border-color: grey;
        }
        .research {
            height: 30px;
            width: 100%;
        }
    }
}
.long-step:hover{

    .description{
        font-weight: 600;
        //font-family: 'Itim', cursive;

    }
}

@media only screen and (max-width: 800px) {
    .long-step{
        height: 180px;
    }
}
