.short-step{
    width: 200px;
    margin: 15px;
    border: solid;
    border-color: white;
    border-width: 10px;
    border-radius: 35px;
    background-color: rgb(253, 243, 243);
    background-color: rgb(255, 255, 255);
    padding: 7px 9px 0 9px ;
    font-family: 'Abel', sans-serif;
    //font-family: 'Della Respira', serif;
    ////font-family: 'Inconsolata', monospace;
    //font-family: 'Itim', cursive;
    font-family: 'Titillium Web', sans-serif;
        color: rgb(46, 45, 45);
    cursor: pointer;

    .title{
        height: 30px;
        padding-bottom: 10px;
        width: 100%;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-direction: column;
        border: solid;
        border-width: 0 0 1px 0;
        font-weight: 800;

    }
    .description {
        height: 70px;
        width: 100%;
    }
    .actions {
        width: 100%;
        .meeting {
            height: 25px;
            width: 85%;
            //border: solid;
            border-width: 1px 0 1px 0;
            border-color: grey;
            overflow: hidden;
            display: flex;
            justify-content: space-between;
            
        }
        .research {
            height: 30px;
            width: 100%;
        }
    }
    .plus-meetings {
        color: rgb(202, 84, 84);
        font-size: 12px;
        font-weight: 800;
        margin-top: -23px;
        margin-left: 170px;
        margin-bottom: 10px;
    }
}
.short-step:hover{
    background-color: rgb(130, 163, 144);
    background-color: rgb(146, 175, 192);
    background-color: white;
    // font-family: 'Itim', cursive;

    .description{
        font-weight: 800;
        //font-family: 'Itim', cursive;
    }
}

.arrow-step{
    border: 0;
    background-color: unset;
    width: 220px;
}
.arrow-step:hover{
    background-color: unset;
}


@media only screen and (max-width: 1500px) {
    .short-step{
        width: 150px;
        font-size: 0.7em;
        margin: 5px;
        border-width: 8px;
        padding: 0 5px ;
        .title{
            height: 40px;
        }
        .actions {
            .meeting {
                height: 20px;
            }
        }
        .plus-meetings {
            font-size: 10px;
            font-weight: 800;
            margin-top: -17px;
            margin-left: 130px;
            margin-bottom: 10px;
        }  
    } 
}

@media only screen and (max-width: 1200px) {
    .short-step{
        width: 130px;
        font-size: 0.55em;
        margin: 4px;
        border-width: 7px;
        padding: 0 4px ;

    } 
}

@media only screen and (max-width: 800px) {
    .short-step{
        width: 130px;
        font-size: 0.45em;
        margin: 4px;
        border-width: 7px;
        padding: 0 4px ;
        height: 180px;
        .title{
            height: 50px;
        }
        border-radius: 10px;
    } 
}


@media only screen and (max-width: 600px) {
    .short-step{
        width: 150px;
        font-size: 0.7em;
        margin: 5px;
        border-width: 8px;
        padding: 0 5px ;

    } 


    .short-step-big-window{
        position: fixed;
        width: 360px;
        height: 70vh;
        overflow-y: scroll;
        padding: 15px 15px ;
        top: 20px;
        left:20px;
        font-family: 'Helvetica', 'Arial', sans-serif;
        font-family: 'Montserrat', sans-serif;
        font-size: 1.1em;
        line-height: 1.25em;
        text-align: justify;
        white-space: pre-line;
        color: rgb(63, 61, 61);
    }
}


.bold{
    font-weight: 800;
}

.little-bold{
    font-weight: 500;
}