.graph-structure {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    overflow: scroll;
    padding-right: 17px; /* Increase/decrease this value for cross-browser compatibility */
    box-sizing: content-box; /* So the width will be 100% + 17px */
    .short-step-row {
        display: flex;
        flex-direction: row;
    }
    .long-step-row {
        display: flex;
        flex-direction: row;
    }

}


.short-step-big-window{
    position: fixed;
    width: 500px;
    height: 70vh;
    overflow-y: scroll;
    padding: 30px 50px ;
    background-color: white;
    top: 50px;
    left:50px;
    font-family: 'Helvetica', 'Arial', sans-serif;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.1em;
    line-height: 1.25em;
    text-align: justify;
    white-space: pre-line;
    color: rgb(63, 61, 61);
    
    .title-line{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .title{
            font-size: 1.8em;
            font-weight: 500;
            text-align: center;
            line-height: 1.2em;
            font-family: 'Titillium Web', sans-serif;
        }
        .close-window{
            cursor: pointer;
            position: fixed;
            width: 30px;
            height: 30px;
            background-color: white;
            border-radius: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            left: 590px;            
        }
    }
    .small-title{
        margin-bottom: 15px;
        font-weight: 700;
    }
    .description{
        margin: 10px 0 15px 0;
        
    }
    .notes{
        margin: 10px 0 10px 0;
    }
    .actions{
        .add-to{
            font-size: 0.7em;
        }
    }
    .image{
        width:90%;
        padding-top: 60px;
        img{
            width: 100%;
        }
    }
}



@media only screen and (max-width: 600px) {

    .graph-structure {
        display: flex;
        flex-direction: column;
        //height: 100%;
        width: 1000px;
        overflow: scroll;
        padding-right: 17px; /* Increase/decrease this value for cross-browser compatibility */
        box-sizing: content-box; /* So the width will be 100% + 17px */
        .short-step-row {
            display: flex;
            flex-direction: row;
        }
        .long-step-row {
            display: flex;
            flex-direction: row;
        }

    }
}
  
