.arrow{
    width: 15px;
    height: 180px;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
}

@media only screen and (max-width: 1500px) {
    .arrow{
        width: 10px;
        font-size: 10px;
        height: 180px;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-direction: column;
    }
}