.container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .main-box {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
}

.left-panel{
    overflow: hidden;
    height: 100vh;
    width: 100%;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}


@media only screen and (max-width: 600px) {
    .container {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .main-box {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }
    }
    .left-panel{
        overflow: scroll;
        //height: 100vh;
        width: 100%;
        -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;  /* Firefox */
    }

}
